import { getMasterToko } from "../../../actions/datamaster_action";
import {
  React,
  connect,
  reduxForm,
  ReanderSelect,
  Field,
  Tabel,
  Loading,
  getData,
  ToastNotification,
  setItem,
  getItem,
} from "../../../components/helpers/library";
import { getDataBaki, GetDataGroup } from "../TambahBarang/Module";
// import { getDataNoTimeOut } from "../../../config/axios";
// import { getDataBaki } from "../../Master/MasterBaki/ModuleMasterBaki";
// import { getMasterGroup } from "../../Master/MasterGroup/Module";

const maptostate = (state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    datagroup: state.datamaster.GetDataGroup,
    dataToko: state.datamaster.getMasterToko,
  };
};

class HeadKirimBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      datajenis: [],
      columns: [
        {
          dataField: "kode_barang",
          text: "Kode Barang ",
          sort: true,
          footerAttrs: {
            colSpan: "7",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>
                Total Semua Barang : {getItem("kirimdatabarang").length || 0}{" "}
              </div>
            );
          },
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "tipe_barang",
          text: "Tipe Barang",
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: "text-right",
          formatter: (row) => {
            return (
              <div className="text-right">
                {" "}
                {row === null || undefined ? row : row.toFixed(3)}{" "}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {parseInt(
                columnData.reduce((acc, item) => acc + item, 0)
              ).toFixed(3)}{" "}
            </div>
          ),
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {}

  componentDidMount() {
    setItem("kirimdatabarang", []);
    this.props.dispatch(getDataBaki());
    this.props.dispatch(GetDataGroup());
    this.props.dispatch(getMasterToko());
  }

  cariJenis(e) {
    getData("group/get/by-kode-group?kode_group=" + e).then((res) => {
      setItem("data_group", res.data);
    });
    getData("jenis/get/by-kode-kategori/" + e)
      .then((res) => {
        this.setState({
          datajenis: res.data,
        });
      })
      .catch((err) => {
        this.setState({
          datajenis: [],
        });
        ToastNotification("info", err.response?.data.message);
      });
  }

  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              label="Kode Group"
              name="kode_group"
              onChange={(e) => {
                this.cariJenis(e);
              }}
              options={this.props.datagroup
                .filter((list) => list.kode_group !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kode  Baki"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            <Field
              label="Kode Jenis"
              name="kode_jenis"
              onChange={(e) => {
                this.props.getJenis(e);
              }}
              options={this.state.datajenis
                .filter((list) => list.kode_dept !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_dept,
                    name: list.kode_dept,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kode Jenis"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4" style={{ display: this.props.active }}>
            <Field
              label="Tujuan"
              name="tujuan"
              options={this.props.dataToko.map((data) => {
                let hasil = {
                  value: data.kode_toko,
                  name: data.kode_toko,
                };
                return hasil;
              })}
              placeholder={"Silahkan Pilih Tujuan"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={this.props.databarang}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          <div
            className="col-lg-12 text-right"
            style={{ display: this.props.active }}
          >
            <button
              disabled={this.props.isLoading}
              className="btn btn-primary"
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Mengirim
                </>
              ) : (
                "Kirim Barang"
              )}
            </button>
          </div>
        </div>

        {this.state.isLoadingBarang ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadKirimBarang = reduxForm({
  form: "HeadKirimBarang",
  enableReinitialize: true,
})(HeadKirimBarang);
export default connect(maptostate)(HeadKirimBarang);
