import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  getDataNoTimeOut2,
  formatDate,
  moment,
  ToastNotification,
  localStorageencryp,
} from "../../../../components/helpers/library";
import HeadLaporanSortirPembelian from "./HeadLaporanSortirPembelian";

class LaporanSortirPembelian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      datasortir: [],
      type : "",
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading : true
    })
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
      type: data.type,
      kode_dept: data.kode_dept,
    };
    getDataNoTimeOut2(
      "sortir-pembelian/reports?type=" +
        data_post.type +
        "&kode_dept=" +
        data.kode_dept +
        "&tgl_to=" +
        data_post.tgl_akhir +
        "&tgl_from=" +
        data_post.tgl_awal
    ).then((res) => {
      // console.log(res.data);
      if (res.data.length === 0) {
        this.setState({
          datasortir: [],
          isLoading: false,
          export: true,
          type : data_post.type,
        });
        ToastNotification("info","Data Laporan Tidak Tersedia")
        return false;
      }
      localStorageencryp("tgl_laporan", JSON.stringify(data_post));
      this.setState({
        datasortir: res.data,
        isLoading: false,
        export: true,
        type : data_post.type,
      });
    }).catch((err) => {
      this.setState({
        isLoading: true,
        export: false,
        type : data_post.type,
      })
      ToastNotification('info',err.response?.data)
    })
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Sortir Pembelian</li>
        </ol>
        <h1 className="page-header">Laporan Sortir Pembelian</h1>
        <Panel>
          <PanelHeader>Laporan Sortir Pembelian</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadLaporanSortirPembelian
              type={this.state.type}
              export={this.state.export}
              data={this.state.datasortir}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>

          <br />
        </Panel>
      </div>
    );
  }
}

LaporanSortirPembelian = reduxForm({
  form: "LaporanSortirPembelian",
  enableReinitialize: true,
})(LaporanSortirPembelian);
export default connect()(LaporanSortirPembelian);
