
import { PanelBody } from "../../../../components/panel/panel";
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  postDataNoTImeOut2,
  ToastNotification,
  reset,
} from "../../../../components/helpers/library";
import FormBahanTukang from "./FormBahanTukang";

class TambahBahanTukang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      settings : "",
    };
  }
  handleSubmit(data){
    this.setState({
      isLoading : true
    })
    let hasil = {
        kode_tukang: data.kode_tukang,
        jenis: data.jenis,
        kode_group: data.kategori,
        jml_bahan: parseInt(data.tambah_bahan),
        keterangan: data.keterangan
    }
    postDataNoTImeOut2('bahan/tambah-bahan',hasil).then((res)=>{
      this.setState({
        isLoading : false
      })
      ToastNotification('success','Tambah Bahan Tersimpan')
      this.props.dispatch(reset('FormBahanTukang'))
    }).catch((err)=>{
      this.setState({
        isLoading : false
      })
      ToastNotification('info',err.response?.data?.message || "Terjadi Kesalahan Saat Mengirim Data")
    })
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Tambah Bahan Tukang</li>
        </ol>
        <h1 className="page-header">Tambah Bahan Tukang </h1>
        <Panel>
          <PanelHeader>Tambah Bahan Tukang</PanelHeader>
         <PanelBody>
            <FormBahanTukang isLoading={this.state.isLoading} onSubmit={(data)=>this.handleSubmit(data)}/>
         </PanelBody>
        </Panel>
      </div>
    );
  }
}

TambahBahanTukang = reduxForm({
  form: "TambahBahanTukang",
  enableReinitialize: true,
})(TambahBahanTukang);
export default connect(null)(TambahBahanTukang);