
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  postDataNoTImeOut2,
  useState,
  ToastNotification,
  reset,
} from "../../../components/helpers/library";
import HeadPembagianPesanan from "./HeadPembagianPesanan";

import {useDispatch} from 'react-redux';
import { setTmpBarang } from "../../../actions/databarang_action";
let PembagianPesanan = () => {

  const [isLoading, setLoading] = useState(false)
  const dispatch = useDispatch();
  const handleSubmit = (data) => {
    // console.log(data);
    let row = {
      no_pesanan: data.no_pesanan,
      kode_tukang: data.kode_tukang,
      ongkos_tukang: parseInt(data.ongkos_tukang),
      berat_susut: parseFloat(data.berat_susut),
    };
    setLoading(true)
    
    postDataNoTImeOut2("pembagian-pesanan",row).then((res)=>{
      ToastNotification("success",'Berhasil Disimpan')
      dispatch(setTmpBarang([]))
      dispatch(reset('HeadPembagianPesanan'))
      setLoading(false);
if (res.data.length !== 0) {
let notapesanan = [];
for (let index = 0; index < 2; index++) {  
let feedback = '';
let keterangan = res.data.slip.keterangan;
feedback += `========================================\n`
feedback += `               NOTA PESANAN             \n`
feedback += `========================================
No Pesamam       : ${res.data.slip.no_pesanan}
Tanggal          : ${res.data.slip.tgl}
Tgl Selesia      : ${res.data.slip.tgl_selesai}
Nama Pemesan     : ${res.data.slip.nama_pemesan}
Alamat           : ${res.data.slip.alamat}
Telepon          : ${res.data.slip.telepon}\n`
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
// res.data.slip.keterangan.forEach((detail, index) => {
feedback += `Kategori           : ${keterangan.kategori}\n`
feedback += `Jenis Pesanan      : ${keterangan.jenis_pesanan}\n`
feedback += `Deskripsi          : ${keterangan.deskripsi}\n`
feedback += `Size               : ${keterangan.size}\n`
feedback += `Atribut            : ${keterangan.atribut}\n`
feedback += `Berat              : ${keterangan.berat.toFixed(3)}\n`
feedback += `Berat Susut        : ${keterangan.berat_susut.toFixed(3)}\n`
feedback += `========================================\n`
// })
feedback += `



`
notapesanan.push(feedback)
}
document.getElementById("nota_pesanan").value = notapesanan.join('');;
}
}).then(()=>{
  const element = document.createElement("a");
  const file = new Blob([document.getElementById("nota_pesanan").value], {
    type: "text/plain;charset=utf-8",
  });
  element.href = URL.createObjectURL(file);
  element.download = "autoprint_pesanan.txt";
  document.body.appendChild(element);
  element.click();
}).catch((err)=>{
  console.log(err);
  ToastNotification("info",err.response?.data?.message)
  setLoading(false);
})
};


  return (
    <div>
        <textarea
            style={{ display: "none" }}
            id="nota_pesanan"
            rows="100"
            cols="100"
          />
      <ol className="breadcrumb float-xl-right">
        <li className="breadcrumb-item">
          <Link to="#">Pesanan</Link>
        </li>
        <li className="breadcrumb-item active">Pembagian Pesanan</li>
      </ol>
      <h1 className="page-header">Pembagian Pesanan </h1>
      <Panel>
        <PanelHeader>Pembagian Pesanan</PanelHeader>
        <br />
        {/* Pembagian Pesanan */}
        <PanelBody>
          <HeadPembagianPesanan isLoading={isLoading} onSubmit={(data) => handleSubmit(data)} />
        </PanelBody>

        <br />
      </Panel>
    </div>
  );
};

PembagianPesanan = reduxForm({
  form: "PembagianPesanan",
  enableReinitialize: true,
})(PembagianPesanan);
export default connect()(PembagianPesanan);
