import { getDataJenis } from "../../../../actions/datamaster_action";
import {
    React,
    connect,
    reduxForm,
    Tabel,
    ReanderSelect,
    InputDate,
    Field,
    getTglSystem,
  } from "../../../../components/helpers/library";
import LaporanKirimSepuhExel from "./exel/LaporanKirimSepuhExel";
import LaporanKirimSepuhPdf from "./pdf/LaporanKirimSepuhPdf";
  

  class HeadKirimSepuh extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        columns : [
            {
              dataField: "tgl_system",
              text: "Tanggal",
              footer : ''
            },
            {
              dataField: "no_sepuh",
              text: "No Sepuh",
              footer : ''
            },
            {
              dataField: "kode_baki",
              text: "Kode Baki",
              footer : ''
            },
            {
                dataField: "berat",
                text: "Berat",
                headerClasses: "text-right",
                formatter: (data) => {
                  return <div className="text-right"> {data.toFixed(2)}</div>;
                },
                footer: (columnData) => (
                  <div className="text-right">
                    {" "}
                    {columnData.reduce((acc, item) => acc + item, 0).toFixed(2)}{" "}
                  </div>
                ),
              },
            {
              dataField: "qty",
              text: "Qty",
              headerClasses: "text-right",
              formatter: (data) => {
                return <div className="text-right"> {data}</div>;
              },
              footer: (columnData) => (
                <div className="text-right">
                  {" "}
                  {columnData.reduce((acc, item) => acc + item, 0)}{" "}
                </div>
              ),
            },
      
            
            {
              dataField: "qty_rusak",
              text: "Qty Rusak",
              headerClasses: "text-right",
              formatter: (data) => {
                return <div className="text-right"> {data}</div>;
              },
              footer: (columnData) => (
                <div className="text-right">
                  {" "}
                  {columnData.reduce((acc, item) => acc + item, 0)}{" "}
                </div>
              ),
            },
            {
              dataField: "berat_rusak",
              text: "Berat Rusak",
              headerClasses: "text-right",
              formatter: (data) => {
                return <div className="text-right"> {data.toFixed(2)}</div>;
              },
              footer: (columnData) => (
                <div className="text-right">
                  {" "}
                  {columnData.reduce((acc, item) => acc + item, 0).toFixed(2)}{" "}
                </div>
              ),
            },
            {
              dataField: "qty_sepuh",
              text: "Qty Sepuh",
              headerClasses: "text-right",
              formatter: (data) => {
                return <div className="text-right"> {data.toFixed(2)}</div>;
              },
              footer: (columnData) => (
                <div className="text-right">
                  {" "}
                  {columnData.reduce((acc, item) => acc + item, 0).toFixed(2)}{" "}
                </div>
              ),
            },
            {
              dataField: "berat_sepuh",
              text: "Berat Sepuh",
              headerClasses: "text-right",
              formatter: (data) => {
                return <div className="text-right"> {data}</div>;
              },
              footer: (columnData) => (
                <div className="text-right">
                  {" "}
                  {columnData.reduce((acc, item) => acc + item, 0)}{" "}
                </div>
              ),
            },
           
          ]
      };
    }
    componentDidMount(){
        this.props.dispatch(getDataJenis())
        this.props.change("kode_dept","ALL")
    }
    setStartDate(date) {
        this.setState({
          tgl_awal: new Date(date),
        });
      }
      setLastDate(date) {
        this.setState({
          tgl_akhir: new Date(date),
        });
      }
    render() {
        let DataJenis = [
            {
              value: "ALL",
              name: "SEMUA",
            },
          ];
          this.props.datajenis.forEach((list) => {
            let hasil = {
              value: list.kode_dept,
              name: list.kode_dept,
            };
            DataJenis.push(hasil);
          });
      
      return (
        <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="kode_dept"
              component={ReanderSelect}
              label="Kode Dept"
              type="text"
              options={DataJenis}
              placeholder="Masukan Kode Dept"
              // customInput={<CustomeInputDate />}
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-3">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>

          <div className="col-lg-3">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="no_sepuh"
              data={this.props.export ? this.props.databarang : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Terima Barang Sepuh"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                type="button"
                  onClick={() => LaporanKirimSepuhPdf(this.props.databarang)}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanKirimSepuhExel data={this.props.databarang} />
              </div>
            </div>
          </div>
        </div>
      </form>
      );
    }
  }
  
  HeadKirimSepuh = reduxForm({
    form: "HeadKirimSepuh",
    enableReinitialize: true,
  })(HeadKirimSepuh);
  export default connect((state) => {
  return {
    datajenis : state.datamaster.getDataJenis,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadKirimSepuh);

  