import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanTerimaBarangSepuh = (data = "") => {
  // initialize jsPDF
  console.log(data);
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];

  //   let footRows = [];
  let finalY = 30;
  doc.setFontSize(15);
  doc.text("LAPORAN TERIMA BARANG SEPUH", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }

  doc.setFontSize(10);
  doc.setProperties({
    title: "Laporan Terima Barang Sepuh",
  });
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );

  //   tableRows.push(tableColumn)
  tableColumn = [
    [
      {
        content: `NO TERIMAS SEPUH`,
      },
      {
        content: `TGL TERIMA`,
      },
      {
        content: `QTY SEPUH`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `QTY TERIMA`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT TERIMA`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT RUSAK`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `QTY RUSAK`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT SELISIH`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BERAT KIRIM`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `QTY KIRIM`,
        styles: {
          halign: "right",
        },
      },
    ],
  ];
  data.forEach((rows, no) => {
    let head = [
      {
        content: `NO FAKTUR : ${rows._id}`,
        colSpan: 12,
        styles : {
            lineWidth: 0.02,
            lineColor: "#000",
        }
      },
    ];

    tableRows.push(head);
    rows.detail.forEach((item, no) => {
      let row = [
        item.no_terima_sepuh,
        item.tgl_terima,
        {
          content: item.qty_sepuh,
          
          styles: {
            halign: "right",
            
          },
        },

        {
          content: item.berat_timbangan.toFixed(2),
          styles: {
            halign: "right",
          },
        },
        {
          content: item.qty_terima,
          styles: {
            halign: "right",
          },
        },
        {
          content: item.berat_terima.toFixed(2),
          styles: {
            halign: "right",
          },
        },
        {
          content: item.berat_rusak.toFixed(2),
          styles: {
            halign: "right",
          },
        },
        {
          content: item.qty_rusak,
          styles: {
            halign: "right",
          },
        },
        {
          content: item.berat_selisih.toFixed(2),
          styles: {
            halign: "right",
          },
        },
        {
          content:
            item.berat_kirim === undefined ? 0 : item.berat_kirim.toFixed(2),
          styles: {
            halign: "right",
          },
        },
        {
          content: item.qty_kirim === undefined ? 0 : item.qty_kirim,
          styles: {
            halign: "right",
          },
        },
      ];
      tableRows.push(row);
    });
  });
  let footer = [
    {
      content: `Sub Total`,
      colSpan: 2,
      styles: {
        halign: "left",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data.reduce(
        (acc, item) => acc + item.detail.reduce((a, b) => a + b.qty_sepuh, 0),
        0
      ),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data
        .reduce(
          (acc, item) =>
            acc + item.detail.reduce((a, b) => a + b.berat_timbangan, 0),
          0
        )
        .toFixed(2),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data.reduce(
        (acc, item) => acc + item.detail.reduce((a, b) => a + b.qty_terima, 0),
        0
      ),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data
        .reduce(
          (acc, item) =>
            acc + item.detail.reduce((a, b) => a + b.berat_terima, 0),
          0
        )
        .toFixed(2),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data
        .reduce(
          (acc, item) =>
            acc + item.detail.reduce((a, b) => a + b.berat_rusak, 0),
          0
        )
        .toFixed(2),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data.reduce(
        (acc, item) => acc + item.detail.reduce((a, b) => a + b.qty_rusak, 0),
        0
      ),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data
        .reduce(
          (acc, item) =>
            acc + item.detail.reduce((a, b) => a + b.berat_selisih, 0),
          0
        )
        .toFixed(2),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: data
        .reduce(
          (acc, item) =>
            acc +
            item.detail.reduce(
              (a, b) => a + (!b.berat_kirim ? 0 : b.berat_kirim),
              0
            ),
          0
        )
        .toFixed(2),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },

    {
      content: data.reduce(
        (acc, item) =>
          acc +
          item.detail.reduce((a, b) => a + (!b.qty_kirim ? 0 : b.qty_kirim), 0),
        0
      ),
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(footer);

  let printed = [
    {
      content: `Printed By ${
        JSON.parse(localStoragedecryp("userdata")).user_id
      } / ${getToday()}`,
      colSpan: 12,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ];
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    // rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02, lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
    //   lineWidth: 0.02,
    //   lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  tableRows = [];
  tableColumn = [];
  finalY = doc.autoTableEndPosY() + 20;

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>Laporan Terima Barang Sepuh</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanTerimaBarangSepuh;
