import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanBahanTukang = (data = "") => {
  // initialize jsPDF
  console.log(data);
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let tableColumn = [];

  //   let footRows = [];
  let finalY = 30;
  doc.text("LAPORAN BAHAN TUKANG DETAIL", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }


  doc.setFontSize(10);
  doc.setProperties({
    title: "Bahan Tukang Detail",
  });
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );

  //   tableRows.push(tableColumn)
  tableColumn = [
    [
      {
        content: `TUKANG`,
      },
      {
        content: `KODE`,
      },
      {
        content: `KATEGORI`,
      
      },
      {
        content: `KETERANGAN`,
       
      },
      {
        content: `ONGKOS`,
      
      },
      {
        content: `SUSUT`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `PATRI`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BAHAN`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `PAKAI`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `PATRI AMB`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `BAHAN AMB`,
        styles: {
          halign: "right",
        },
      },
      {
        content: `SISA BAHAN`,
        styles: {
          halign: "right",
        },
      },
    ],
  ];
  data.forEach((rows, no) => {
    // let head = [
    //   {
    //     content: `NO FAKTUR : ${rows._id}`,
    //     colSpan: 12,
    //     styles : {
    //         lineWidth: 0.02,
    //         lineColor: "#000",
    //     }
    //   },
    // ];

    // tableRows.push(head);
    let row = [
      rows.kode_tukang,
      rows.kode_trx,
      rows.detail_kategori,
      rows.keterangan,
      {
        content: rows.ongkos,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.susut,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.patri,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.bahan,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.pakai,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.patri_amb,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.bahan_amb,
        
        styles: {
          halign: "right",
          
        },
      },
      {
        content: rows.sisa_bhn,
        
        styles: {
          halign: "right",
          
        },
      },
    ];
    tableRows.push(row);
  });
  let total_ongkos = 0;
  let total_susut = 0;
  let total_patri = 0;
  let total_bahan = 0;
  let total_pakai = 0;
  let total_patri_amb = 0;
  let total_bahan_amb = 0;
  // let total_sisa_bhn = 0;
  data.forEach(item => {
    let ongkos = 0;
    let susut = 0;
    let patri = 0;
    let bahan = 0;
    let pakai = 0;
    let patri_amb = 0;
    let bahan_amb = 0;
    // let sisa_bhn = 0;

    if (item.ongkos === '-') {
      ongkos = 0;
    }else{
      ongkos = item.ongkos;
    }
    total_ongkos += ongkos;
    
    if (item.susut === '-') {
      susut = 0;
    }else{
      susut = item.susut;
    }
    total_susut += susut;

    if (item.patri === '-') {
      patri = 0;
    }else{
      patri = item.patri;
    }
    total_patri += patri;

    if (item.bahan === '-') {
      bahan = 0;
    }else{
      bahan = item.bahan;
    }
    total_bahan += bahan;

    if (item.pakai === '-') {
      pakai = 0;
    }else{
      pakai = item.pakai;
    }
    total_pakai += pakai;

    if (item.patri_amb === '-') {
      patri_amb = 0;
    }else{
      patri_amb = item.patri_amb;
    }
    total_patri_amb += patri_amb;

    if (item.bahan_amb === '-') {
      bahan_amb = 0;
    }else{
      bahan_amb = item.bahan_amb;
    }
    total_bahan_amb += bahan_amb;

    // if (item.sisa_bhn === '-') {
    //   sisa_bhn = 0;
    // }else{
    //   sisa_bhn = item.sisa_bhn;
    // }
    // total_sisa_bhn += sisa_bhn;
  });
  let footer = [
    {
      content: `Grand Total : `,
      colSpan: 4,
      styles: {
        halign: "left",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_ongkos,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_susut,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_patri,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_bahan,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_pakai,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_patri_amb,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: total_bahan_amb,
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: '',
      styles: {
        halign: "right",
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(footer);

  let printed = [
    {
      content: `Printed By ${
        JSON.parse(localStoragedecryp("userdata")).user_id
      } / ${getToday()}`,
      colSpan: 12,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ];
  tableRows.push(printed);

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    // rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02, lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
    //   lineWidth: 0.02,
    //   lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  tableRows = [];
  tableColumn = [];
  finalY = doc.autoTableEndPosY() + 20;

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>Bahan Tukang Detail</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanBahanTukang;
