import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { HiidenFiled, ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { NumberOnly, Gramasi } from "../../../Validasi/normalize";

const maptoState = (state) => {
  if (state.KirimBarangDariBos.shoModalTambahBarang !== null) {
    return {
      kategori : state.datamaster.GetDataGroup,
      jenis : state.datamaster.getDataJenis,
      loadkategori : state.KirimBarangDariBos.shoModalTambahBarang.kategori || [],
      loadjenis : state.KirimBarangDariBos.shoModalTambahBarang.jenis || [],
      initialValues: {
        kategori : state.KirimBarangDariBos.shoModalTambahBarang.kategori,
        jenis : state.KirimBarangDariBos.shoModalTambahBarang.jenis,
        detail_jenis : state.KirimBarangDariBos.shoModalTambahBarang.detail_jenis,
        asal_barang : state.KirimBarangDariBos.shoModalTambahBarang.asal_barang,
        qty : state.KirimBarangDariBos.shoModalTambahBarang.qty,
        berat : state.KirimBarangDariBos.shoModalTambahBarang.berat,
        index : state.KirimBarangDariBos.shoModalTambahBarang.index,
      },
    };
  }
};
class DetailBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data_kategori : ''
    };
  }

  componentDidMount() {
    // console.log();
    if (this.props.loadjenis) {
      this.setState({
        data_kategori : this.props.loadkategori
      })
      this.props.change('jenis', this.props.loadjenis)

    }
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              name="index"
              type="hidden"
              component={HiidenFiled}
            />
            <Field
              label="Kategori"
              name="kategori"
              options={this.props.kategori
                .filter((list) => list.kode_group !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group,
                  };
                  return data;
                })}
              onChange={(e) => this.setState({ data_kategori: e })}
              placeholder={"Silahkan Pilih Kategori"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              label="Jenis"
              name="jenis"
              options={this.props.jenis
                .filter((list) => list.kode_group === this.state.data_kategori)
                .map((list) => {
                  let data = {
                    value: list.kode_dept,
                    name: list.kode_dept
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Jenis"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-4">
            <Field
              name="detail_jenis"
              component={ReanderField}
              label="Detail Jenis"
              type="text"
              placeholder="Masukan Detail Jenis"
            />
          </div>
          <div className="col-4">
            <Field
              name="asal_barang"
              component={ReanderField}
              label="Asal Barang"
              type="text"
              placeholder="Masukan Asal Barang"
            />
          </div>
          <div className="col-4">
            <Field
              name="qty"
              component={ReanderField}
              label="Qty"
              normalize={NumberOnly}
              type="text"
              placeholder="Masukan Qty"
            />
          </div>
          <div className="col-4">
            <Field
              name="berat"
              component={ReanderField}
              label="Berat"
              normalize={Gramasi}
              type="text"
              placeholder="Masukan Berat"
            />
          </div>
          <div className="col-12 text-right">
            <label> &nbsp; </label>
            <br />
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoadingTambahBarang}
              type="submit"
            >
              {this.props.isLoadingTambahBarang ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

DetailBarang = reduxForm({
  form: "DetailBarang",
  enableReinitialize: true,
})(DetailBarang);
export default connect(maptoState)(DetailBarang);
